$theme-variables-primary-color: violet;
$theme-variables-primary-shade: 7;

:root {
  /**
   * Font settings
   */
  --theme-line-height: 1.55;
  --theme-font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  --theme-font-family-monospace: ui-monospace, SFMono-Regular, Menlo, Monaco,
    Consolas, Liberation Mono, Courier New, monospace;
  --theme-font-family-headings: -apple-system, BlinkMacSystemFont, Segoe UI,
    Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  --theme-heading-font-weight: 700;

  /**
   * Font sizes
   */
  --theme-font-size-xs: clamp(12px, 0.75em, 14px);
  --theme-font-size-sm: clamp(14px, 0.875em, 16.33px);
  --theme-font-size-md: clamp(16px, 1em, 18.67px);
  --theme-font-size-lg: clamp(18px, 1.125em, 21px);
  --theme-font-size-xl: clamp(20px, 1.25em, 23.33px);

  /**
   * Header font sizes
   */
  --theme-h1-line-height: 1.3;
  --theme-h2-line-height: 1.35;
  --theme-h3-line-height: 1.4;
  --theme-h4-line-height: 1.45;
  --theme-h5-line-height: 1.5;
  --theme-h6-line-height: 1.5;
  --theme-h1-font-size: clamp(34px, 2.125rem, 39.67px);
  --theme-h2-font-size: clamp(26px, 1.625rem, 30.33px);
  --theme-h3-font-size: clamp(22px, 1.375rem, 25.67px);
  --theme-h4-font-size: clamp(18px, 1.125rem, 21px);
  --theme-h5-font-size: clamp(16px, 1rem, 18.67px);
  --theme-h6-font-size: clamp(14px, 0.875rem, 16.33px);

  /**
   * Border radius
   */
  --theme-radius-xs: 0.125rem;
  --theme-radius-sm: 0.25rem;
  --theme-radius-md: 0.5rem;
  --theme-radius-lg: 1rem;
  --theme-radius-xl: 2rem;

  /**
   * Spacing
   */
  --theme-spacing-xs: 0.625rem;
  --theme-spacing-sm: 0.75rem;
  --theme-spacing-md: 1rem;
  --theme-spacing-lg: 1.25rem;
  --theme-spacing-xl: 1.5rem;

  /**
   * Colours
   */
  --theme-color-white: #fff;
  --theme-color-black: #000;

