  --theme-color-primary-0: var(--theme-color-cyan-0);
  --theme-color-primary-1: var(--theme-color-cyan-1);
  --theme-color-primary-2: var(--theme-color-cyan-2);
  --theme-color-primary-3: var(--theme-color-cyan-3);
  --theme-color-primary-4: var(--theme-color-cyan-4);
  --theme-color-primary-5: var(--theme-color-cyan-5);
  --theme-color-primary-6: var(--theme-color-cyan-6);
  --theme-color-primary-7: var(--theme-color-cyan-7);
  --theme-color-primary-8: var(--theme-color-cyan-8);
  --theme-color-primary-9: var(--theme-color-cyan-9);
  --theme-color-primary-default: var(--theme-color-cyan-6);

  /**
  * Shadows
  */
  --theme-shadow-xs: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    0 0.0625rem 0.125rem rgba(0, 0, 0, 0.1);
  --theme-shadow-sm: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 0.625rem 0.9375rem -0.3125rem,
    rgba(0, 0, 0, 0.04) 0 0.4375rem 0.4375rem -0.3125rem;
  --theme-shadow-md: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 1.25rem 1.5625rem -0.3125rem,
    rgba(0, 0, 0, 0.04) 0 0.625rem 0.625rem -0.3125rem;
  --theme-shadow-lg: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 1.75rem 1.4375rem -0.4375rem,
    rgba(0, 0, 0, 0.04) 0 0.75rem 0.75rem -0.4375rem;
  --theme-shadow-xl: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 2.25rem 1.75rem -0.4375rem,
    rgba(0, 0, 0, 0.04) 0 1.0625rem 1.0625rem -0.4375rem;

  /**
   * Animations
   */
