.errorWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: var(--theme-spacing-xl);
  padding: calc(var(--theme-spacing-xl) * 2);
  min-height: 320px;
  text-align: center;
  width: 100%;
  height: 100%;
  background: var(--theme-color-dark-9);
  overflow: auto;

  :global(.font) {
    color: var(--theme-color-white);
  }

  svg {
    height: 48px;
    width: 48px;
  }

  .title {
  }

  .subtitle {
  }

  .description {
    max-width: 380px;
  }
}
