:root {
  --theme-font-family: 'Nunito Sans', sans-serif;
  --theme-header-font-family: 'Syncopate', sans-serif;
  --theme-header-letter-spacing: 6px;
  --theme-color-primary-0: var(--theme-color-teal-0);
  --theme-color-primary-1: var(--theme-color-teal-1);
  --theme-color-primary-2: var(--theme-color-teal-2);
  --theme-color-primary-3: var(--theme-color-teal-3);
  --theme-color-primary-4: var(--theme-color-teal-4);
  --theme-color-primary-5: var(--theme-color-teal-5);
  --theme-color-primary-6: var(--theme-color-teal-6);
  --theme-color-primary-7: var(--theme-color-teal-7);
  --theme-color-primary-8: var(--theme-color-teal-8);
  --theme-color-primary-9: var(--theme-color-teal-9);
  --theme-color-primary-default: var(--theme-color-teal-6);
}

@import 'styles/sass/normalise';
@import 'styles/sass/overrides';
@import 'styles/sass/toast';
@import 'styles/sass/colors';
