@use 'sass:map';

$xs: 'xs';
$sm: 'sm';
$md: 'md';
$lg: 'lg';
$xl: 'xl';

$breakpoints-em: (
  #{$xs}: '36em',
  #{sm}: '48em',
  #{md}: '62em',
  #{lg}: '75em',
  #{xl}: '88em'
);

@mixin theme-media-query($breakpoint) {
  @media only screen and (max-width: #{map.get($breakpoints-em, $breakpoint)}) {
    @content;
  }
}

/** Export the breakpoints to make them accessible to JS */
:export {
  @each $bpkey, $bp in $breakpoints-em {
    #{$bpkey}: $bp;
  }
}
