@use 'styles/sass/variables/layout-vars.module.scss';

.contentWrapper {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-y: hidden;
  overscroll-behavior-y: none;
  --header-bar-height: #{layout-vars.$header-height};
  background: var(--theme-color-background-base-1);
  color: var(--theme-color-text-base-1);
}

.contentBelowHeader {
  height: 100%;
  top: 0;
  position: relative;
  overflow-y: auto;
}

