// BASE
:root {
  /** States */
  --theme-state-hover-opacity: 0.8;

  /** Buttons */
  --theme-button-hover-opacity: var(--theme-state-hover-opacity);
  --theme-button-radius: var(--theme-radius-sm);
  --theme-button-shadow: var(--theme-shadow-base-md);

  /* Header */
  --theme-header-padding: var(--theme-spacing-sm);

  /* Header Links */
  --theme-header-link-background: unset;
  --theme-header-link-shadow: unset;
  --theme-header-link-radius: var(--theme-radius-sm);
  --theme-header-link-font-size: var(--theme-font-size-sm);
  --theme-header-link-hover-opacity: 0.7;
}

// COLOURS - LIGHT THEME
:root {
  /** Background colour base */
  --theme-color-background-base-1: var(--theme-color-white);
  --theme-color-background-base-2: var(--theme-color-gray-3);

  --theme-color-background-base-1-i: var(--theme-color-dark-9);
  --theme-color-background-base-2-i: var(--theme-color-dark-6);

  /** Shadows */
  --theme-shadow-base-xs: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    0 0.0625rem 0.125rem rgba(0, 0, 0, 0.1);
  --theme-shadow-base-sm: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 0.625rem 0.9375rem -0.3125rem,
    rgba(0, 0, 0, 0.04) 0 0.4375rem 0.4375rem -0.3125rem;
  --theme-shadow-base-md: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 1.25rem 1.5625rem -0.3125rem,
    rgba(0, 0, 0, 0.04) 0 0.625rem 0.625rem -0.3125rem;
  --theme-shadow-base-lg: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 1.75rem 1.4375rem -0.4375rem,
    rgba(0, 0, 0, 0.04) 0 0.75rem 0.75rem -0.4375rem;
  --theme-shadow-base-xl: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 2.25rem 1.75rem -0.4375rem,
    rgba(0, 0, 0, 0.04) 0 1.0625rem 1.0625rem -0.4375rem;

  /** Text colour base */
  --theme-color-text-base-1: var(--theme-color-dark-9);
  --theme-color-text-base-2: var(--theme-color-dark-5);

  --theme-color-text-base-1-i: var(--theme-color-white);
  --theme-color-text-base-2-i: var(--theme-color-gray-3);

  /** Title text colour base */
  --theme-color-title-base-1: var(--theme-color-dark-9);
  --theme-color-title-base-2: var(--theme-color-dark-5);

  --theme-color-title-base-1-i: var(--theme-color-white);
  --theme-color-title-base-2-i: var(--theme-color-gray-3);

  /** Card background colour base */
  --theme-color-card-background-base-1: var(--theme-color-gray-1);

  /** Card text colour base */
  --theme-color-card-text-base-1: var(--theme-color-dark-5);

  /** Button background colour base */
  --theme-color-button-background-base-1: var(--theme-color-dark-5);
  --theme-color-button-background-base-2: var(--theme-color-gray-0);

  --theme-color-button-background-base-1-i: var(--theme-color-white);
  --theme-color-button-background-base-2-i: var(--theme-color-gray-3);

  /** Button text colour base */
  --theme-color-button-text-base-1: var(--theme-color-white);
  --theme-color-button-text-base-2: var(--theme-color-dark-4);

  --theme-color-button-text-base-1-i: var(--theme-color-dark-9);
  --theme-color-button-text-base-2-i: var(--theme-color-dark-5);

  /** Button background primary */
  --theme-color-button-background-primary: var(--theme-color-primary-default);

  /** Button text colour primary */
  --theme-color-button-text-primary: var(--theme-color-white);

  /** Pill border colour base */
  --theme-color-pill-border-base-1: var(--theme-color-gray-5);
  --theme-color-pill-border-base-2: var(--theme-color-gray-4);

  --theme-color-pill-border-base-1-i: var(--theme-color-gray-4);
  --theme-color-pill-border-base-2-i: var(--theme-color-primary-4);

  /** Pill border colour primary */
  --theme-color-pill-border-primary: var(--theme-color-primary-9);

  /* Header */
  --theme-color-header-background: var(--theme-color-primary-7);
  --theme-color-header-text: var(--theme-color-white);

  /** Checkbox */
  --theme-color-checkbox-background: var(--theme-color-white);
  --theme-color-checkbox-icon: var(--theme-color-dark-5);

  /** Modal */
  --theme-color-modal-text: var(--theme-color-white);

  /** Slider */
  --theme-color-slider-progress: var(--theme-color-primary-6);
  --theme-color-slider-track: var(--theme-color-gray-3);
}

[data-theme='dark'] {
  /** Background colour base */
  --theme-color-background-base-1: var(--theme-color-dark-8);
  --theme-color-background-base-2: var(--theme-color-dark-7);

  --theme-color-background-base-1-i: var(--theme-color-dark-white);
  --theme-color-background-base-2-i: var(--theme-color-dark-gray-3);

  /** Shadows */
  --theme-shadow-base-xs: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    0 0.0625rem 0.125rem rgba(0, 0, 0, 0.1);
  --theme-shadow-base-sm: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 0.625rem 0.9375rem -0.3125rem,
    rgba(0, 0, 0, 0.04) 0 0.4375rem 0.4375rem -0.3125rem;
  --theme-shadow-base-md: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 1.25rem 1.5625rem -0.3125rem,
    rgba(0, 0, 0, 0.04) 0 0.625rem 0.625rem -0.3125rem;
  --theme-shadow-base-lg: 0 0.0625rem 0.1875rem rgba(0, 0, 0, 0.05),
    rgba(0, 0, 0, 0.05) 0 1.75rem 1.4375rem -0.4375rem,
    rgba(0, 0, 0, 0.04) 0 0.75rem 0.75rem -0.4375rem;
  --theme-shadow-base-xl: 0 0.0625rem 0.1875rem rgba(255, 255, 255, 0.05),
    rgba(255, 255, 255, 0.05) 0 2.25rem 1.75rem -0.4375rem,
    rgba(255, 255, 255, 0.04) 0 1.0625rem 1.0625rem -0.4375rem;

  /** Text colour base */
  --theme-color-text-base-1: var(--theme-color-white);
  --theme-color-text-base-2: var(--theme-color-gray-3);

  --theme-color-text-base-1-i: var(--theme-color-dark-9);
  --theme-color-text-base-2-i: var(--theme-color-dark-5);

  /** Title text colour base */
  --theme-color-title-base-1: var(--theme-color-white);
  --theme-color-title-base-2: var(--theme-color-gray-3);

  --theme-color-title-base-1-i: var(--theme-color-dark-9);
  --theme-color-title-base-2-i: var(--theme-color-dark-5);

  /** Card background colour base */
  --theme-color-card-background-base-1: var(--theme-color-dark-6);

  /** Card text colour base */
  --theme-color-card-text-base-1: var(--theme-color-gray-3);

  /** Button background colour base */
  --theme-color-button-background-base-1: var(--theme-color-white);
  --theme-color-button-background-base-2: var(--theme-color-dark-4);

  --theme-color-button-background-base-1-i: var(--theme-color-dark-5);
  --theme-color-button-background-base-2-i: var(--theme-color-dark-3);

  /** Button text colour base */
  --theme-color-button-text-base-1: var(--theme-color-dark-9);
  --theme-color-button-text-base-2: var(--theme-color-gray-1);

  --theme-color-button-text-base-1-i: var(--theme-color-white);
  --theme-color-button-text-base-2-i: var(--theme-color-gray-3);

  /** Button background primary */
  --theme-color-button-background-primary: var(--theme-color-primary-default);

  /** Button text colour primary */
  --theme-color-button-text-primary: var(--theme-color-white);

  /** Pill border colour base */
  --theme-color-pill-border-base-1: var(--theme-color-gray-5);
  --theme-color-pill-border-base-2: var(--theme-color-gray-6);

  --theme-color-pill-border-base-1-i: var(--theme-color-gray-6);
  --theme-color-pill-border-base-2-i: var(--theme-color-primary-4);

  /** Pill border colour primary */
  --theme-color-pill-border-primary: var(--theme-color-primary-4);

  /* Header */
  --theme-color-header-background: var(--theme-color-primary-7);
  --theme-color-header-text: var(--theme-color-white);

  /** Checkbox */
  --theme-color-checkbox-background: var(--theme-color-dark-7);
  --theme-color-checkbox-icon: var(--theme-color-gray-3);

  /** Modal */
  --theme-color-modal-text: var(--theme-color-white);

  /** Slider */
  --theme-color-slider-progress: var(--theme-color-primary-9);
  --theme-color-slider-track: var(--theme-color-gray-8);
}
