/** Apply global styles */
body {
  color: var(--theme-color-text-base-1);
  background: var(--theme-color-background-base-1);
}

body,
button {
  font-family: var(--theme-font-family);
  line-height: var(--theme-line-height);
}

.font {
  line-height: var(--theme-line-height);
  font-size: var(--theme-font-size-md);
  font-weight: 400;

  &.inherit {
    font-weight: inherit;
    text-decoration: inherit;
  }

  &.bold500 {
    font-weight: 600;
  }

  &.bold {
    font-weight: 700;
  }

  &.notBold {
    font-weight: 400;
  }

  &.italic {
    font-style: italic;
  }

  &.underline {
    text-decoration: underline;
  }

  &.useHeaderFont {
    font-family: 'Archivo Black', sans-serif;
  }

  &.base {
    color: var(--theme-color-text-base-1);

    &.light {
      color: var(--theme-color-text-base-2);
    }

    &.inverse {
      color: var(--theme-color-text-base-1-i);
    }
  }

  &.primary {
    color: var(--theme-color-text-primary);
  }
}

.header {
  font-family: var(--theme-header-font-family, var(--theme-font-family));
  letter-spacing: var(--theme-header-letter-spacing);

  &.useHeaderFont {
    font-family: 'Archivo Black', sans-serif;
  }

  &:not(.notBold) {
    font-weight: 700;
  }
}

.font {
  &.inherit {
    font-size: inherit;
    line-height: inherit;
  }

  &.font-size-xs {
    font-size: var(--theme-font-size-xs);
  }

  &.font-size-sm {
    font-size: var(--theme-font-size-sm);
  }

  &.font-size-md {
    font-size: var(--theme-font-size-md);
  }

  &.font-size-lg {
    font-size: var(--theme-font-size-lg);
  }

  &.font-size-xl {
    font-size: var(--theme-font-size-xl);
  }

  &.font-size-h1 {
    font-size: var(--theme-h1-font-size);
    line-height: var(--theme-h1-line-height);
  }

  &.font-size-h2 {
    font-size: var(--theme-h2-font-size);
    line-height: var(--theme-h2-line-height);
  }

  &.font-size-h3 {
    font-size: var(--theme-h3-font-size);
    line-height: var(--theme-h3-line-height);
  }

  &.font-size-h4 {
    font-size: var(--theme-h4-font-size);
    line-height: var(--theme-h4-line-height);
  }

  &.font-size-h5 {
    font-size: var(--theme-h5-font-size);
    line-height: var(--theme-h5-line-height);
  }

  &.font-size-h6 {
    font-size: var(--theme-h6-font-size);
    line-height: var(--theme-h6-line-height);
  }
}
